import React from 'react'
import { ContainerPrincial } from './styled'

const Comments = (props) => {

    return (

        <ContainerPrincial>
        
            <p><strong>Nome: </strong>{props.index.name}</p>
            <p><strong>Comentário: </strong>{props.index.comment}</p>
 
        </ContainerPrincial>

    )
}

export default Comments